import axiosInstance from "@/app/lib/axiosInstance";
import { JobApplication, JobI, LoginData, SignupData, UserI } from "./props";



export const fetchUserApplications = async (): Promise<JobApplication[]> => {
  const res = await axiosInstance.get("/applications/my");
  return res.data;
};

export const applyForJob = async (jobId: string): Promise<{ message: string }> => {
  const res = await axiosInstance.post(`/applications/${jobId}/apply`);
  return res.data;
};


export const loginUser = async (data: LoginData) => {
  const res = await axiosInstance.post("/auth/login", data);
  return res.data;
};

export const signupUser = async (data: SignupData) => {
  const res = await axiosInstance.post("/auth/signup", data);
  return res.data;
};

export const fetchJobs = async (): Promise<JobI[]> => {
  const res = await axiosInstance.get("/jobs");
  return res.data;
};


export const fetchJobById = async (id: number): Promise<JobI> => {
  const res = await axiosInstance.get(`/jobs/${id}`);
  return res.data;
};

export const createJob = async (job: Omit<JobI, "id">) => {
  const res = await axiosInstance.post("/jobs", job);
  return res.data;
};

export const updateJob = async ({ id, ...data }: JobI) => {
  const res = await axiosInstance.put(`/jobs/${id}`, data);
  return res.data;
};

export const deleteJob = async (id: number) => {
  const res = await axiosInstance.delete(`/jobs/${id}`);
  return res.data;
};





export const fetchUsers = async (): Promise<UserI[]> => {
  const res = await axiosInstance.get("/users");
  return res.data;
};

export const fetchUserById = async (id: string): Promise<UserI> => {
  const res = await axiosInstance.get(`/users/${id}`);
  return res.data;
};

export const updateUser = async (id: string, data: Partial<UserI>) => {
  const res = await axiosInstance.put(`/users/update/${id}`, data);
  return res.data;
};

export const deleteUser = async (id: string) => {
  const res = await axiosInstance.delete(`/users/delete/${id}`);
  return res.data;
};

export const getMe = async (): Promise<UserI> => {
  const res = await axiosInstance.get("/auth/me");
  return res.data;
};
